<template>
  <div
    class="home-container pa-6"
    id="home"
    :class="[screenWidth > 720 ? 'ml-perc15' : 'mt-10']"
  >
    <div class="console-container">
      <span id="text"></span>
      <div class="console-underscore" id="console">&#95;</div>
    </div>

    <div class="py-8">
      I've been working as a software engineer for 2 years now, and I have to
      say, writing code can be a real rollercoaster ride. Sometimes I feel like
      I'm on top of the world when everything is running smoothly, but other
      times I'm ready to throw my computer out the window when I can't figure
      out why my code isn't working. But hey, at least I can blame all my
      problems on my imaginary friend, Debuggy McBugface! I get so lost in my
      work that I forget to eat or sleep, but I figure if I can debug my way
      through a tough problem, I can probably figure out how to survive without
      food and water, right?
    </div>
    <div>You want to find more about me ? GOOO DEEP DOWN !!!</div>
    <div class="py-5" style="border-bottom: 3px dashed blueviolet"></div>
    <div class="py-12 add-container animated-text">
      <div>BURAK KAGAN OZKAN</div>
    </div>
    <div class="py-2" style="border-bottom: 3px dashed blueviolet"></div>
    <SummaryPage id="summary" class="py-4" />
    <div class="py-5" style="border-bottom: 3px dashed blueviolet"></div>
    <ExperiencesPage id="experiences" class="py-4" />
    <div class="py-5" style="border-bottom: 3px dashed blueviolet"></div>
    <ProjectsPage id="project" class="py-4" />
    <div class="py-5" style="border-bottom: 3px dashed blueviolet"></div>
    <ContactPage id="contact" class="py-4" />
  </div>
</template>

<script>
import SummaryPage from "./SummaryPage.vue";
import ExperiencesPage from "./ExperiencesPage.vue";
import ProjectsPage from "./ProjectsPage.vue";
import ContactPage from "./ContactPage.vue";
import { mapGetters } from "vuex";
export default {
  components: {
    SummaryPage,
    ExperiencesPage,
    ProjectsPage,
    ContactPage,
  },
  mounted() {
    this.HelloText(["Hello World"], "text");
  },
  methods: {
    HelloText(words, id) {
      var visible = true;
      var con = document.getElementById("console");
      var letterCount = 1;
      var x = 1;
      var waiting = false;
      var target = document.getElementById(id);
      setInterval(() => {
        if (letterCount === 0 && waiting === false) {
          waiting = true;
          target.innerHTML = words[0].substring(0, letterCount);
          window.setTimeout(function () {
            var usedWord = words.shift();
            words.push(usedWord);
            x = 1;
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (letterCount === words[0].length + 1 && waiting === false) {
          waiting = true;
          window.setTimeout(function () {
            x = -1;
            letterCount += x;
            waiting = false;
          }, 1000);
        } else if (waiting === false) {
          target.innerHTML = words[0].substring(0, letterCount);
          letterCount += x;
        }
      }, 120);
      setInterval(function () {
        if (visible === true) {
          con.className = "console-underscore hidden";
          visible = false;
        } else {
          con.className = "console-underscore";

          visible = true;
        }
      }, 400);
    },
  },
  computed: {
    ...mapGetters({
      screenWidth: "getScreenWidth",
    }),
  },
};
</script>

<style lang="scss">
.home-container {
  color: greenyellow;
  .add-container {
    line-height: 96px;
    font-size: 76px;
    color: rgba($color: #ff0101, $alpha: 1);
    font-family: digital-dream-bold;
    text-align: center;

    &.animated-text {
      animation-name: text-flicker;
      animation-duration: 2s;
      animation-timing-function: linear;
      animation-iteration-count: infinite;
    }
  }
}
.deep-down {
}
.ml-perc15 {
  margin-left: 15%;
}
@keyframes text-flicker {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.75;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.hidden {
  opacity: 0;
}
.console-container {
  font-family: digital-dream-bold;
  font-size: 2.75em;
}
.console-underscore {
  display: inline-block;
  position: relative;
  top: -0.14em;
  left: 10px;
}
</style>
