<template>
  <v-col cols="12" md="3" class="project-item ma-8 pa-1">
    <v-img
      class="mb-3"
      :src="images[0]"
      @click="openCareousel()"
      style="cursor: pointer"
    >
    </v-img>

    <h4 class="my-2">{{ title }}</h4>
    <p>
      {{ description }}
    </p>
    <v-btn color="orange" class="github-btn" @click="routeToPage()" rel="dofollow"
      >Github Page</v-btn
    >
    <v-dialog v-model="openDialog">
      <v-carousel hide-delimiters height="70%">
        <v-carousel-item
          v-for="(image, i) in images"
          :key="i"
          :src="image"
          cover
        ></v-carousel-item>
      </v-carousel>
    </v-dialog>
  </v-col>
</template>

<script>
export default {
  components: {},
  props: {
    images: Array,
    title: String,
    description: String,
    githubLink: String,
  },
  data() {
    return {
      openDialog: false,
    };
  },
  methods: {
    openCareousel() {
      this.openDialog = true;
    },
    routeToPage() {
      window.open(this.githubLink, "_blank");
    },
  },
};
</script>

<style lang="scss">
.project-item {
  border: 2px dashed rgb(255, 255, 0);
  display: grid;
  p {
    height: 200px;
    overflow: hidden;
  }
  p:hover {
    overflow-y: scroll;
  }

  .github-btn {
    font-family: digital-dream-bold;
  }
}
</style>
