      <v-progress-linear
        model-value="10"
        v-model="currentValue"
        color="orange"
        height="10"
        striped
      ></v-progress-linear>
    </v-col>
  </v-row>
</template>

<script>
export default {
  props: {
    skill: String,
    value: Number,
  },
  data() {
    return {
      currentValue: this.value,
    };
  },
};
</script>

